import React from 'react';
import api from '../Global/Api';
import {Form,Input,Radio,Button,Modal,InputNumber} from 'antd';
import {Row, Col, Card} from 'react-bootstrap';
import Aux from "../../hoc/_Aux";



 class BankDetailsForm extends React.Component {
    constructor(props){
      super(props);
      this.state ={
        id:'',
        name_on_acc:'',
        owner_add:'',
        bank_ifsc:'',
        bank_name:'',
        bank_acc:'',
        bank_acc_type:'',
        approve_by:'',
        bank_detail_status:'',
        bankdetail:[],
        UserId : this.props.match.params.UserId,
      }
    }
    componentDidMount() {
      this.fetchbank();
    }

    fetchbank = (params = {}) => {
      this.setState({ loading: true });
      api.get(`/api/bank?user=${this.state.UserId}`,{
         params: {
              ...params,
            },
            type: 'json',
        }).then(res => {
          if(res.data.length>0)
            this.setState({
              loading: false,
              id:res.data[0].id,
              name_on_acc:res.data[0].name_on_acc,
              owner_add:res.data[0].owner_add,
              bank_acc:res.data[0].bank_acc,
              bank_ifsc:res.data[0].bank_ifsc,
              bank_name:res.data[0].bank_name,
              bank_acc_type:res.data[0].bank_acc_type,
              approve_by:res.data[0].approve_by,
              bank_detail_status:res.data[0].bank_detail_status,
            });
        });
    };


    success() {
      Modal.success({
        content: 'Successively Updated',
      });
    }
    
    error() {
      Modal.error({
        title: 'This is an error message',
        content: 'Error Occurred!!',
      });
    }

    handleSubmit = (e) => {
      e.preventDefault();
      this.props.form.validateFieldsAndScroll((err, value) => {
        if (!err) {
          console.log('Received values of form: ', value);
          this.setState({loading:true});
          if(this.props.isEditProduct){
            api.put(`/api/bank/${this.props.bankdetail.id}`, value, {
            headers: { 'Content-Type': 'application/json' }, body: JSON.stringify(value)})
            .then(response => {
              if(response.status === 200){
                  this.success();
              }
            })
            .catch(error => {
              this.error();
            });
          }
            else{
              api.post(`/api/bank`, value, {
            headers: { 'Content-Type': 'application/json' }, body: JSON.stringify(value)})
            .then(response => {
              if(response.status === 201){
                  this.success();
              }
            })
            .catch(error => {
              this.error();
            });
            }
         }
      });
    };

   
    render() {
      const { getFieldDecorator } = this.props.form;
  
      const formItemLayout = {
        labelCol: {
          xs: { span: 24 },
          sm: { span: 8 },
        },
        wrapperCol: {
          xs: { span: 24 },
          sm: { span: 16 },
        },
      };
      const tailFormItemLayout = {
        wrapperCol: {
          xs: {
            span: 24,
            offset: 0,
          },
          sm: {
            span: 16,
            offset: 8,
          },
        },
      };
      return (
        <Aux>
        <Row>
            <Col>
                <Card>
                    <Card.Header>
                        <Card.Title as="h5"> Bank Details</Card.Title>
                    </Card.Header>
                    <Card.Body>
                        <Row>
                            <Col md={2} ></Col>
                            <Col md={8} >
                              <Form {...formItemLayout} onSubmit={this.handleSubmit}>
                                <Form.Item label="Name on Acc.">
                                  {getFieldDecorator('name_on_acc', { initialValue: this.state.name_on_acc,
                                    rules: [
                                      {
                                        required: true,
                                      },
                                    ],
                                  })(<Input />)}
                                </Form.Item>
                                
                                <Form.Item label="User" style={{ display: 'none' }}>
                                  {getFieldDecorator('user', { initialValue: this.state.UserId,
                                    rules: [
                                      {
                                        required: true, 
                                      },
                                    ],
                                  })(<Input style={{}}/>)}
                                </Form.Item>


                                <Form.Item label="Account Number">
                                {getFieldDecorator('bank_acc', { initialValue: this.state.bank_acc,
                                  rules: [{ 
                                    required: true,
                                    message: 'Please input your Account Number!'
                                  }],
                                })(<InputNumber  style={{width: '100%' }}/>)}
                              </Form.Item>

                              
                              <Form.Item label="Bank IFSC">
                                {getFieldDecorator('bank_ifsc', { initialValue: this.state.bank_ifsc,
                                  rules: [{ 
                                    required: true,
                                    message: 'Please input your Bank IFSC!'
                                  }],
                                })(<Input/>)}
                              </Form.Item>

                              <Form.Item label="Bank Name">
                                {getFieldDecorator('bank_name', { initialValue: this.state.bank_name,
                                  rules: [{ 
                                    required: true,
                                    message: 'Please input your Bank Name!'
                                  }],
                                })(<Input/>)}
                              </Form.Item>

                              <Form.Item label="Bank Acc. Type">
                                {getFieldDecorator('bank_acc_type', { initialValue: this.state.bank_acc_type,
                                  rules: [{ 
                                    required: true,
                                    message: 'Please input your Bank Name!'
                                  }],
                                })(<Input/>)}
                              </Form.Item>
                                
                                <Form.Item label="Status" hasFeedback {...formItemLayout}>
                                  {getFieldDecorator('bank_detail_status', { initialValue: this.state.bank_detail_status,
                                    rules: [
                                      {
                                        required: true,
                                        type: 'boolean',
                                      },
                                    ],
                                  })(
                                    <Radio.Group name="radiogroup">
                                      <Radio value={true}>
                                        Active
                                      </Radio >
                                      <Radio value={false}>
                                        Disable
                                      </Radio >
                                    </Radio.Group>
                                  )}
                                </Form.Item>
                                

                                <Form.Item {...tailFormItemLayout}>
                                  <Button type="primary" htmlType="submit">
                                    Submit
                                  </Button>
                                </Form.Item>
                              </Form></Col>
                                </Row>
                            </Card.Body>
                        </Card>
                    </Col>
                </Row>
            </Aux>
      );
    }
  }
  
  const WrappedBankDetailsForm = Form.create({ name: 'register' })(BankDetailsForm);
   
  export default WrappedBankDetailsForm;

